import { Component, OnInit } from '@angular/core';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { DataService } from '../data.service';

@Component({
  selector: 'app-add-emp',
  templateUrl: './add-emp.component.html',
  styleUrls: ['./add-emp.component.css'],
})
export class AddEmpComponent implements OnInit {
  addEmp: FormGroup;
  submitted = false;
  depts = ['CS', 'IS'];
  constructor(private fb: FormBuilder, private ds: DataService) {
    this.addEmp = fb.group({
      name: ['', Validators.required],
      dept: ['', Validators.required],
      bio: [''],
    });
  }

  ngOnInit(): void {}

  get f(): any {
    return this.addEmp.controls;
  }

  onSubmit(): void {
    console.log('form submitted');
    this.submitted = true;

    if (this.addEmp.invalid) return;
    let e = this.addEmp.value;
    this.ds.addEmployee(e.name, e.dept, e.bio).subscribe((res: any) => {
      console.log(res);
      alert('Employee added successfully!\nEmployeeID: ' + res['emp_code']);
    });
  }

  onReset(): void {
    this.submitted = false;
    this.addEmp.reset();
  }
}
