import { Component, Input, OnChanges, OnInit } from '@angular/core';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { DataService } from '../data.service';

@Component({
  selector: 'app-edit-emp',
  templateUrl: './edit-emp.component.html',
  styleUrls: ['./edit-emp.component.css'],
})
export class EditEmpComponent implements OnInit, OnChanges {
  @Input() emp: any;

  editEmp: FormGroup;
  depts = ['CS', 'IS'];
  submitted = false;

  constructor(private fb: FormBuilder, private ds: DataService) {
    this.editEmp = fb.group({
      dept: ['', Validators.required],
      bio: [''],
      resetPass: [this.emp?.resetPassword],
    });
  }

  ngOnInit(): void {}

  ngOnChanges(): void {
    console.log(this.emp);
    const selectedDept = this.depts.find((dept) => dept === this.emp?.dept);
    this.editEmp.controls['dept'].setValue(selectedDept, { onlySelf: true });
    this.editEmp.controls['bio'].setValue(this.emp?.bio, { onlySelf: true });
    this.editEmp.controls['resetPass'].setValue(this.emp?.resetPassword, {
      onlySelf: true,
    });
  }

  get f(): any {
    return this.editEmp.controls;
  }

  onSubmit(): void {
    console.log('form submitted');
    this.submitted = true;

    if (this.editEmp.invalid) return;

    let e = this.editEmp.value;
    this.ds
      .updateEmployee(e.dept, e.bio, e.resetPassword)
      .subscribe((res: any) => {
        console.log(res);
        // alert('Employee added successfully!\nEmployeeID: ' + res['emp_code']);
      });
  }
}
